body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: -webkit-flex;
  display: flex;
}

.user-settings {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex: 2 1;
          flex: 2 1;
  background-color: #eaeaea;
}

.user-settings > button {
}

main {
  -webkit-flex: 8 1;
          flex: 8 1;
}

/* form {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
}

.tm-input-label {

} */

input {
  margin: 0 1rem;
}

input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.daily-lift {
  width: 95%;
  margin: auto;
}

.daily-lift-t1,
.daily-lift-t2 {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.lift-title {
  grid-column: 1/3;
  width: 100%;
}

.workouts {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  height: 100%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.selected {
  color: white;
  background-color: #000;
}

.accessory-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.accessory {
  border-top: 1px gray solid;
  border-bottom: 1px gray solid;
  display: -webkit-flex;
  display: flex;
}
.accessory > p {
  -webkit-flex: 9 1;
          flex: 9 1;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.accessory > p > span {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.accessory > button {
  -webkit-flex: 1 1;
          flex: 1 1;
  max-width: 25px;
}

.accessory-selector {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.accessory-selector > select {
  margin-left: 1rem;
}

